/**
 * 
 * @method extractList
 * @param  {Object}    collection 
 * @param  {String}    key        
 * @return {Array}
 */

module.exports = function(collection, key){
  return _.chain(collection)
    // 
    .map(function(item){
      return item[key];
    })
    // null
    .compact()
    // 
    .flatten()
    // 
    .uniq()
    .value();
}
